/* ## Typography
--------------------------------------------- */

* {
	-webkit-tap-highlight-color: #111;
}

::selection {
	background-color: $dark;
	color: $white;
}

html {
	font-size: 62.5%; // 10px browser default.
	max-width: 100vw;
	overflow-x: hidden;
}

body {
	color: $dark;
	font-family: $font-body;
	font-size: 1.8rem;
	font-weight: $normal;
	line-height: 1.618; // Golden ratio.
	-webkit-font-smoothing: antialiased;
	-moz-font-smoothing: grayscale;
	margin: 0;
	max-width: 100vw;
	overflow-x: hidden;
	@include breakpoint(sm) {
		font-size: 1.9rem;
	}
}

body>div {
	font-size: 1.8rem;
	@include breakpoint(sm) {
		font-size: 1.9rem;
	}
}

p,
ul,
ol {
	margin: 0 0 1.382em;
	padding: 0;
}

a {
	color: $action;
	word-wrap: break-word;
	text-decoration: none;
	&:hover,
	&:focus {
		color: $dark;
		outline: none;
	}
}

ul,
ol {
	margin-left: 1.5em;
}

hr {
	border: 0;
	border-collapse: collapse;
	border-bottom: $site-border;
	clear: both;
	padding: 1em 0 0;
	margin: 0 0 2em;
}

b,
strong {
	font-weight: $bold;
}

blockquote,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	border-left: 4px solid $accent;
	margin: 1em 0;
	padding: 0 1em;
}


/* ## Headings
--------------------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-heading;
	font-weight: $bolder;
	line-height: 1.236; // Golden ratio.
	margin: 0 0 0.618em;
	text-transform: uppercase;
}

h1 {
	font-size: 2em;
	text-transform: none;
}

h2 {
	font-size: 1.6em;
}

h3 {
	font-size: 1.4em;
}

h4 {
	font-size: 1.2em;
}

h5 {
	font-size: 1.1em;
}

h6 {
	font-size: 1em;
}


/* ## Forms
--------------------------------------------- */

input,
select,
textarea {
	background-color: $white;
	border: $site-border;
	border-radius: $site-radius;
	padding: 0.5em 1em;
	width: 100%;
	line-height: 2;
	&:focus {
		outline: none;
		border-color: $accent;
	}
}

:focus {
	outline: $site-border;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
	width: auto;
}

input[type="search"] {
	-webkit-appearance: none;
}

::-moz-placeholder {
	color: $dark;
	opacity: 1;
}

::-webkit-input-placeholder {
	color: $dark;
}

.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	@extend %button;
	@include transition;
	&:hover,
	&:focus {
		background-color: $dark;
		color: white;
	}
	&.small {
		font-size: 1.2rem;
	}
	&.large {
		font-size: 1.8rem;
	}
	&:disabled {
		background-color: $light;
		border-width: 0;
		color: darken($light, 50%);
		cursor: not-allowed;
	}
	+button,
	+.button {
		margin-top: 1em;
		clear: both;
		@include breakpoint(xs) {
			margin-top: 0;
			clear: none;
		}
	}
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
	display: none;
}

fieldset {
	margin-bottom: 1em;
}

label {
	display: block;
}


/* ## Objects
--------------------------------------------- */

embed,
iframe,
img,
object,
video,
.wp-caption,
.wp-video-shortcode {
	max-width: 100%;
	display: block;
}

img {
	height: auto;
}

figure {
	margin: 0;
}

video {
	object-fit: cover;
}

.wp-video-shortcode {
	width: auto !important;
}


/* ## Tables
--------------------------------------------- */

table {
	background-color: $white;
	border: $site-border;
	border-collapse: collapse;
	border-spacing: 0;
	line-height: 1;
	margin-bottom: 4rem;
	width: 100%;
}

tbody {
	border-bottom: $site-border;
}

td,
th {
	text-align: left;
	padding: 0.618em;
	border-top: $site-border;
	border-right: $site-border;
}


/* ## Accessibility
--------------------------------------------- */

.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut,
.wp-custom-logo .site-title,
.wp-custom-logo .site-description {
	position: absolute !important;
	clip: rect(0, 0, 0, 0);
	height: 0.1rem;
	width: 0.1rem;
	border: 0;
	overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
	clip: auto !important;
	height: auto;
	width: auto;
	display: block;
	padding: 1rem 2rem;
	z-index: 100000;
	text-decoration: none;
	box-shadow: 0 0 0.2rem 0.2rem rgba($dark, 0.1);
}

.genesis-skip-link {
	margin: 0;
	li {
		height: 0;
		width: 0;
		list-style: none;
	}
}
