/* ## Widgets
--------------------------------------------- */

.widget {
	margin-bottom: 6vw;
	word-wrap: break-word;
	background-color: $white;
	&:last-of-type {
		margin-bottom: 0;
	}
	.sidebar & {
		margin-bottom: 0;
		margin-top: 5vw;
		font-size: 1.6rem;
		padding: 6vw;
		@include breakpoint(sm) {
			margin-top: 4rem;
			padding: 4rem;
		}
		&:first-of-type {
			@include breakpoint(md) {
				margin-top: 0;
			}
		}
	}
	p {
		&:last-of-type {
			margin-bottom: 0;
		}
	}
	ul>li:last-of-type:not(.gfield) {
		margin-bottom: 0;
	}
	ul>li {
		margin-bottom: 0.5em;
		padding-bottom: 0.5em;
	}
	ul>li:last-of-type {
		padding-bottom: 0;
	}
	ol>li {
		list-style-position: inside;
		list-style-type: decimal;
		padding-left: 1em;
		text-indent: -1em;
	}
	li li {
		border: 0;
		margin: 0 0 0 1em;
		padding: 0;
	}
}

.widget-title {
	font-family: $font-heading;
	font-size: 1.5rem;
	text-transform: none;
	background: $accent;
	font-weight: 700;
	padding: 1.2rem 1.5rem;
	margin-bottom: 2rem;
}

.widget-wrap {
	ul {
		list-style: none;
	}
}

.widget_calendar {
	table {
		width: 100%;
	}
	td,
	th {
		text-align: center;
	}
}
