.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"], .call-to-action a, .front-page-1 .wrapper .widget:first-child a {
  background-color: #FC5C65;
  border: 0;
  border-radius: 0.3rem;
  color: #ffffff;
  cursor: pointer;
  font-weight: 700;
  padding: 1em 2em;
  text-decoration: none;
  display: inline-block;
  width: auto;
  height: auto;
  line-height: 1;
}

.call-to-action a, .front-page-1 .wrapper .widget:first-child a {
  transform: translateY(5px);
}

.call-to-action a:hover, .front-page-1 .wrapper .widget:first-child a:hover, .call-to-action a:focus, .front-page-1 .wrapper .widget:first-child a:focus {
  transform: translateY(-5px);
}

:root {
  --hero-image: url("/assets/images/pug.jpg");
