/* ## Box Sizing
--------------------------------------------- */
html,
input[type="search"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* ## Float Clearing
--------------------------------------------- */
.author-box,
.clearfix,
.entry,
.entry-content,
.footer-widgets,
.nav-primary,
.nav-secondary,
.pagination,
.site-container,
.site-footer,
.site-header,
.site-inner,
.widget,
.wrap {
  clear: both;
}

/* ## Typography
--------------------------------------------- */
* {
  -webkit-tap-highlight-color: #111;
}

::selection {
  background-color: #404040;
  color: #ffffff;
}

html {
  font-size: 62.5%;
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  color: #404040;
  font-family: "Muli", Helvetica, Arial, sans-serif;
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.618;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
  margin: 0;
  max-width: 100vw;
  overflow-x: hidden;
}

@media (min-width: 768px) {
  body {
    font-size: 1.9rem;
  }
}

body > div {
  font-size: 18px;
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  body > div {
    font-size: 1.9rem;
  }
}

p,
ul,
ol {
  margin: 0 0 1.382em;
  padding: 0;
}

a {
  color: #FC5C65;
  word-wrap: break-word;
  text-decoration: none;
}

a:hover, a:focus {
  color: #404040;
  outline: none;
}

ul,
ol {
  margin-left: 1.5em;
}

hr {
  border: 0;
  border-collapse: collapse;
  border-bottom: 2px solid #eeeeee;
  border-bottom: 0.2rem solid #eeeeee;
  clear: both;
  padding: 1em 0 0;
  margin: 0 0 2em;
}

b,
strong {
  font-weight: 800;
}

blockquote,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  border-left: 4px solid #ffed7f;
  margin: 1em 0;
  padding: 0 1em;
}

/* ## Headings
--------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Nunito Sans", Helvetica, Arial, sans-serif;
  font-weight: 700;
  line-height: 1.236;
  margin: 0 0 0.618em;
  text-transform: uppercase;
}

h1 {
  font-size: 2em;
  text-transform: none;
}

h2 {
  font-size: 1.6em;
}

h3 {
  font-size: 1.4em;
}

h4 {
  font-size: 1.2em;
}

h5 {
  font-size: 1.1em;
}

h6 {
  font-size: 1em;
}

/* ## Forms
--------------------------------------------- */
input,
select,
textarea {
  background-color: #ffffff;
  border: 2px solid #eeeeee;
  border: 0.2rem solid #eeeeee;
  border-radius: 0.3rem;
  padding: 0.5em 1em;
  width: 100%;
  line-height: 2;
}

input:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #ffed7f;
}

:focus {
  outline: 2px solid #eeeeee;
  outline: 0.2rem solid #eeeeee;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
  width: auto;
}

input[type="search"] {
  -webkit-appearance: none;
}

::-moz-placeholder {
  color: #404040;
  opacity: 1;
}

::-webkit-input-placeholder {
  color: #404040;
}

.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.button:hover, .button:focus,
button:hover,
button:focus,
input[type="button"]:hover,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:focus {
  background-color: #404040;
  color: white;
}

.button.small,
button.small,
input[type="button"].small,
input[type="reset"].small,
input[type="submit"].small {
  font-size: 12px;
  font-size: 1.2rem;
}

.button.large,
button.large,
input[type="button"].large,
input[type="reset"].large,
input[type="submit"].large {
  font-size: 18px;
  font-size: 1.8rem;
}

.button:disabled,
button:disabled,
input[type="button"]:disabled,
input[type="reset"]:disabled,
input[type="submit"]:disabled {
  background-color: #f4f5f6;
  border-width: 0;
  color: #6a7681;
  cursor: not-allowed;
}

.button + button,
.button + .button,
button + button,
button + .button,
input[type="button"] + button,
input[type="button"] + .button,
input[type="reset"] + button,
input[type="reset"] + .button,
input[type="submit"] + button,
input[type="submit"] + .button {
  margin-top: 1em;
  clear: both;
}

@media (min-width: 512px) {
  .button + button,
  .button + .button,
  button + button,
  button + .button,
  input[type="button"] + button,
  input[type="button"] + .button,
  input[type="reset"] + button,
  input[type="reset"] + .button,
  input[type="submit"] + button,
  input[type="submit"] + .button {
    margin-top: 0;
    clear: none;
  }
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
  display: none;
}

fieldset {
  margin-bottom: 1em;
}

label {
  display: block;
}

/* ## Objects
--------------------------------------------- */
embed,
iframe,
img,
object,
video,
.wp-caption,
.wp-video-shortcode {
  max-width: 100%;
  display: block;
}

img {
  height: auto;
}

figure {
  margin: 0;
}

video {
  object-fit: cover;
}

.wp-video-shortcode {
  width: auto !important;
}

/* ## Tables
--------------------------------------------- */
table {
  background-color: #ffffff;
  border: 2px solid #eeeeee;
  border: 0.2rem solid #eeeeee;
  border-collapse: collapse;
  border-spacing: 0;
  line-height: 1;
  margin-bottom: 40px;
  margin-bottom: 4rem;
  width: 100%;
}

tbody {
  border-bottom: 2px solid #eeeeee;
  border-bottom: 0.2rem solid #eeeeee;
}

td,
th {
  text-align: left;
  padding: 0.618em;
  border-top: 2px solid #eeeeee;
  border-top: 0.2rem solid #eeeeee;
  border-right: 2px solid #eeeeee;
  border-right: 0.2rem solid #eeeeee;
}

/* ## Accessibility
--------------------------------------------- */
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut,
.wp-custom-logo .site-title,
.wp-custom-logo .site-description {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  height: 0.1rem;
  width: 1px;
  width: 0.1rem;
  border: 0;
  overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
  clip: auto !important;
  height: auto;
  width: auto;
  display: block;
  padding: 10px 20px;
  padding: 1rem 2rem;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 0.2rem 0.2rem rgba(64, 64, 64, 0.1);
}

.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link li {
  height: 0;
  width: 0;
  list-style: none;
}

/* ## Site Containers
--------------------------------------------- */
.content-sidebar-wrap,
.wrap {
  margin: 0 auto;
  padding-left: 5%;
  padding-right: 5%;
}

@media (min-width: 768px) {
  .content-sidebar-wrap,
  .wrap {
    width: 90%;
    max-width: 1280px;
    padding-left: 0;
    padding-right: 0;
  }
}

.content-sidebar-wrap .wrap,
.wrap .wrap {
  max-width: 100%;
  width: auto;
  padding: 0;
}

.site-inner {
  clear: both;
}

.page-builder .site-inner {
  border-top: 2px solid #eeeeee;
  border-top: 0.2rem solid #eeeeee;
  border-bottom: 2px solid #eeeeee;
  border-bottom: 0.2rem solid #eeeeee;
}

.content-sidebar-wrap {
  max-width: 1280px;
  padding-top: 5%;
  padding-bottom: 5%;
}

@media (min-width: 896px) {
  .content-sidebar-wrap {
    padding: 4rem 0;
  }
}

@media (min-width: 896px) {
  .content-sidebar-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .content-sidebar-wrap:before, .content-sidebar-wrap:after {
    display: none;
  }
  .sidebar-content .content-sidebar-wrap {
    flex-direction: row-reverse;
  }
}

/* ## Content Widths and Positions
--------------------------------------------- */
@media (min-width: 896px) {
  .content {
    width: 100%;
  }
  .sidebar-primary {
    width: 25%;
  }
  .sidebar-content .content,
  .content-sidebar .content {
    width: calc(75% - 4rem);
  }
  .home .content {
    width: 100%;
  }
}

