/* ## Gallery
--------------------------------------------- */
.gallery,
.gallery-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.gallery a {
  display: block;
  min-height: 100%;
}

.gallery img {
  height: auto;
  width: 100%;
  display: block;
}

.gallery-caption {
  display: block;
}

.gallery-item {
  width: 100%;
  margin: 0 0 5vw;
  text-align: center;
  display: block;
}

@media (min-width: 512px) {
  .gallery-item {
    width: 48.717948717948715%;
    margin-bottom: 2.564102564102564%;
  }
}

/* ## Avatars
--------------------------------------------- */
.avatar {
  border-radius: 50%;
  margin-right: 20px;
  margin-right: 2rem;
  float: left;
}

.alignright .avatar {
  margin-left: 20px;
  margin-left: 2rem;
}

/* ## Genesis
--------------------------------------------- */
.breadcrumb {
  padding: 1em 6vw;
  background-color: #ffffff;
  border-bottom: 2px solid #eeeeee;
  border-bottom: 0.2rem solid #eeeeee;
  font-size: 14px;
  font-size: 1.4rem;
  width: 100%;
}

