/* ## Menus
--------------------------------------------- */
.genesis-nav-menu {
  line-height: 1;
  width: 100%;
  z-index: 2;
}

.genesis-nav-menu .menu-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.genesis-nav-menu a {
  color: #404040;
  display: block;
  font-size: 15px;
  font-size: 15px;
  font-size: 1.5rem;
  font-weight: 400;
  outline-offset: -1px;
  padding-bottom: 12px;
  padding-top: 12px;
  text-decoration: none;
}

.genesis-nav-menu a:focus,
.genesis-nav-menu a:hover,
.genesis-nav-menu .current-menu-item > a,
.genesis-nav-menu .sub-menu .current-menu-item > a:focus,
.genesis-nav-menu .sub-menu .current-menu-item > a:hover {
  color: #FC5C65;
  text-decoration: none;
}

.genesis-nav-menu .sub-menu,
.genesis-nav-menu .sub-menu a {
  width: 90%;
}

.genesis-nav-menu .sub-menu {
  display: none;
  padding-left: 15px;
}

.genesis-nav-menu .sub-menu a {
  background: #fff;
  font-size: 14px;
  font-size: 14px;
  font-size: 1.4rem;
  position: relative;
  word-wrap: break-word;
}

.genesis-nav-menu .menu-item:focus,
.genesis-nav-menu .menu-item:hover {
  position: relative;
}

.genesis-nav-menu .menu-item:hover > .sub-menu {
  left: auto;
  opacity: 1;
}

.menu,
.sub-menu {
  margin-left: 0;
}

/* Responsive Menu
--------------------------------------------- */
.menu .menu-item > a:focus + ul.sub-menu,
.menu .menu-item.sfHover > ul.sub-menu {
  left: auto;
  opacity: 1;
}

.genesis-responsive-menu {
  display: none;
  padding-bottom: 15px;
}

.menu-toggle,
.sub-menu-toggle {
  background-color: transparent;
  border-width: 0;
  color: #404040;
  display: block;
  margin: 0;
  overflow: hidden;
  text-align: center;
  visibility: visible;
}

.menu-toggle:focus,
.menu-toggle:hover,
.sub-menu-toggle:focus,
.sub-menu-toggle:hover {
  background-color: transparent;
  border-width: 0;
  color: #FC5C65;
}

.menu-toggle {
  line-height: 20px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 15px 0;
  display: flex;
  align-items: center;
}

.menu-toggle.activated::before {
  content: "\f406";
  font-family: 'IonIcons';
  font-size: 24px;
}

.site-header .menu-toggle::before {
  margin-right: 5px;
}

.sub-menu-toggle {
  padding: 9px 10px;
}

.sub-menu .sub-menu-toggle {
  padding: 12px 10px;
}

.sub-menu-toggle::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-transform: rotate(0);
  -ms-transform: rotate(0);
  transform: rotate(0);
  transition: transform .25s ease-in-out;
}

.sub-menu-toggle.activated::before {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

/* Header Menu
--------------------------------------------- */
.nav-primary {
  clear: left;
  width: 100%;
}

/* Footer Menu
--------------------------------------------- */
.nav-secondary {
  margin-top: 10px;
}

.nav-secondary .genesis-nav-menu {
  line-height: 1.5;
}

.nav-secondary .menu-item {
