  }
  .genesis-nav-menu .sub-menu a {
    padding-bottom: 1.5rem;
    padding-top: 1.5rem;
  }
  .genesis-nav-menu .sub-menu .sub-menu {
    margin: -36px 0 0 178px;
  }
  /* Responsive Menu
	--------------------------------------------- */
  .genesis-responsive-menu {
    display: block;
    padding-top: 15px;
  }
  .menu-toggle,
  .sub-menu-toggle {
    display: none;
    visibility: hidden;
  }
  /* Header Menu
	--------------------------------------------- */
  .nav-primary {
